<template>
  <div class="header-div">
    <nav class="header-nav">
        <ul>
            <li class="btn"><a href="#home">Home</a></li>
            <li class="btn"><a href="#projects">Projects</a></li>
            <li class="btn">SkillSet</li>
            <li class="btn"><a href="#certificates">Certificates</a></li>
            <li class="btn"><a href="#contacts">Contacts</a></li>
        </ul>
    </nav>

    <hamburgerNav/>
  </div>
</template>

<script>
import hamburgerNav from './hamburger-nav.vue'

export default {
name: 'header-nav',
components: {hamburgerNav},
setup(){
  const navActive = () => {
        const nav = document.querySelector("nav");
        nav.classList.toggle("active")
    }

  return {navActive}
}
}
</script>

<style scoped>
.header-div{
    height: 70px;
    width: 100vw;
    background-color: white;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: fixed;
    font-weight: 400;
    z-index: 1;
}

nav{
    margin-right: 50px;
    letter-spacing: 1px;
}

nav ul{
    margin: 0;
    display: flex;
    align-items: center;
    list-style-type: none;
    gap: 15px;
    font-size: 20px;
}

.btn {
  text-transform: uppercase;
  padding: 5px 10px;
  font-size: 17px;
  font-weight: 400;
  background: transparent;
  position: relative;
  transition: all 1s;
  overflow: hidden;
  border-radius: 5px;
}

.btn:hover,
.btn:hover a {
  color: white;
  cursor: pointer;
  transition: all 1s;
}
.btn a{
  text-decoration: none;
  color: black;
  transition: all 1s;
}

.btn::before {
  content: '';
  position: absolute;
  height: 100%;
  width: 0%;
  top: 0;
  left: -40px;
  transform: skewX(45deg);
  background-color: #3a85ffdb;
  z-index: -1;
  transition: all 1s;
  border-radius: 5px;
}

.btn:hover::before {
  width: 220%;
}

#menuToggle{
  display: none;
}

@media only screen and (max-width: 768px) {
  .header-nav{
    display: none;
    z-index: -1;
  }

  #menuToggle{
    display: block;
  }

  nav{
    position: absolute;
    top: 70px;
    margin-right: unset;
    width: 100%;
    background-color: rgba(245, 245, 245, 0.512);
  }

  nav ul{
      display: flex;
      flex-direction: column;
      padding: unset;
      gap: unset;
  }
  .btn{
    width: 100%;
    text-align: center;
    padding: 10px 0;
    border-radius: 0;
    transition: all 500ms;
  }

  .header-nav.active{
    display: block;
  }

}
</style>