<template>
    <div class="landing-div" id="home">
        <headerNavVue/>
        <div class="landing-elements">
            <div class="socials">
                <div class="fb-div"><a href="https://www.facebook.com/samuel.omila.7.1/" target="_blank" class="fa fa-facebook"></a></div>
                <div class="twitter-div"><a href="https://twitter.com/SammyO1O" target="_blank" class="fa fa-twitter"></a></div>
                <div class="insta-div"><a href="https://www.instagram.com/summyow/?hl=en" target="_blank" class="fa fa-instagram"></a></div>
                <div class="linked-div"><a href="https://www.linkedin.com/in/samuel-omila-907390243/" target="_blank" class="fa fa-linkedin"></a></div>
            </div>
             <div class="img-div">
            </div>

            <div class="typewriter">
                <h1>Hi! I am <span class="typewriter-name">Sam</span>.</h1>
                <h2><span class="auto-type"></span></h2>
            </div>
        </div>
    </div>
</template>

<script>
import headerNavVue from './header-nav.vue'
import { onMounted } from 'vue'
export default {
name: 'landingSection',
components: {
    headerNavVue
},
setup(){
    onMounted(() => {
        setTimeout(()=>{
            var typed = new Typed(".auto-type", {
                strings: ["Welcome to my e-portfolio", "I am a web-developer", "I enjoy learning!", "Your visit is much appreciated!"],
                typeSpeed: 150,
                backSpeed: 50,
                loop: true
            })
        }, 5500)


        const FBDIV = document.querySelector(".fb-div")
        const FAFACEBOOK = document.querySelector(".fa-facebook")

        FAFACEBOOK.addEventListener("mouseenter", () => {
            FBDIV.style.backgroundColor = getComputedStyle(FAFACEBOOK).backgroundColor;
        })

        FAFACEBOOK.addEventListener("mouseleave", () => {
            FBDIV.style.backgroundColor = "";
        })

        const TWITTERDIV = document.querySelector(".twitter-div")
        const FATWITTER = document.querySelector(".fa-twitter")

        FATWITTER.addEventListener("mouseenter", () => {
            TWITTERDIV.style.backgroundColor = getComputedStyle(FATWITTER).backgroundColor;
        })

        FATWITTER.addEventListener("mouseleave", () => {
            TWITTERDIV.style.backgroundColor = "";
        })

        const INSTADIV = document.querySelector(".insta-div")
        const FAISNTA = document.querySelector(".fa-instagram")

        FAISNTA.addEventListener("mouseenter", () => {
            INSTADIV.style.backgroundImage = getComputedStyle(FAISNTA).backgroundImage;
        })

        FAISNTA.addEventListener("mouseleave", () => {
            INSTADIV.style.backgroundImage = "";
        })

        const LINKEDDIV = document.querySelector(".linked-div")
        const FALINKED = document.querySelector(".fa-linkedin")

        FALINKED.addEventListener("mouseenter", () => {
            LINKEDDIV.style.backgroundColor = getComputedStyle(FALINKED).backgroundColor;
        })

        FALINKED.addEventListener("mouseleave", () => {
            LINKEDDIV.style.backgroundColor = "";
        })
    });
}

}
</script>

<style scoped>
.landing-div{
    height: 100vh;
    width: 100vw;
    background-color: whitesmoke;
    z-index: 1;
}

.landing-elements{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
}

.img-div{
    height: 250px;
    width: 250px;
    background-image: url('../assets/mypic.avif');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50%;
    transform: scaleX(-1);
    box-shadow: rgba(0, 0, 0, 0.45) 0px 50px 50px -20px;
    margin-left: 20%;
}

.socials{
    display: flex;
    flex-direction: column;
}

.fa {
  padding: 12px;
  font-size: 30px;
  width: 54px;
  margin: 5px 0;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
  transition: all 0.7s ease-in-out;
  box-shadow: 0px 0px 7px black;
}

.fa-facebook {
  background: #3B5998;
  color: white;
}

.fa-twitter {
  background: #55ACEE;
  color: white;
}

.fa-instagram {
  background-image: linear-gradient(to bottom, #F58529, #DD2A7B, #8134AF);
  color: white;
}

.fa-linkedin {
  background: #007bb5;
  color: white;
}

.fb-div, .twitter-div, .insta-div, .linked-div{
    background-color: #156eff;
    width: 70px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border-radius: 0 50% 50% 0;
    padding: 3px 8px 3px 0;
    margin: 5px 0;
    transition: all 0.7s ease-in-out;
}

.fa:hover{
    transform: rotate(360deg) scale(1.05);
    transition: all 0.5s ease-in-out;
}

.typewriter{
    margin-left: 200px;
}


.typewriter h1 {
  color: black;
  
  font-size: 3rem;
  font-family: monospace;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .15em solid orange; /* The typewriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em; /* Adjust as needed */
  animation: typing 4s steps(20, end),
             blink-caret 1s step-end infinite;
}

.typewriter-name{
    color: #3a85ffdb;
}

.typewriter h2{
    letter-spacing: 1.5px;
}

/* The typing effect */
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange }
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .landing-div{
    overflow: hidden;
  }
  .fa {
    padding: 12px;
    font-size: 20px;
    width: 44px;
    margin-left: 5px;
   }
   .fb-div, .twitter-div, .insta-div, .linked-div{
     width: 60px;
    }
    .img-div{
     width: 200px;
     height: 200px;
     margin: auto;
     z-index: 0;
    }

    .typewriter{
    position: absolute;
    top: 80vh;
    margin-left: unset;
    width: 100%;
    text-align: center;
    }

    .typewriter h1 {
        font-size: 2.5rem;
        width: fit-content;
    }

    .typewriter h2 {
        font-size: 1.5rem;
        
    }
}
</style>