<template>
  <div class="project-section" id="projects">
    <div class="weather project reveal fade-left">
        <weather/>
        <div class="weather-caption">
          <div class="caption-overlay">
            <div class="caption-tile"></div>
            <div class="caption-tile"></div>
            <div class="caption-tile"></div>
            <div class="caption-tile"></div>
          </div>
          <p class="weather-text">
            This application uses Vue.js, Vuetify, CSS, and a weather API to provide accurate 
            and up-to-date weather information for any location around the world.
            One of the best features of this application is its ability to detect a 
            user's location automatically, making it incredibly easy to get weather 
            information for wherever they are at any time. Additionally, the application 
            allows users to input any location they want, making it easy to check weather 
            conditions for upcoming travel or simply keep tabs on weather conditions for different parts of the world.
          </p>
        </div>
    </div>
    <div class="calculator project reveal fade-left">
      
      <captionOverlayVue/>
      <calculatorVue/>
    </div>
    <div class="otherProjects">
      <div class="hove-instruct">
        <span class="text">Hover to View</span>
      </div>
      <div class="projectslide project reveal fade-left">
        <otherProjectVue/>
      </div>
    </div>

    <div class="certificateDiv">
      <div class="hove-instruct">
        <span class="text">Certificates</span>
      </div>
      <div class="certificate project reveal fade-left" id="certificates">
        <certificateVue/>
      </div>
    </div>
    
  </div>
</template>

<script>
import { onMounted } from 'vue'
import weather from './weather.vue'
import calculatorVue from './calculator.vue'
import captionOverlayVue from './caption-overlay.vue'
import otherProjectVue from './otherProject.vue'
import certificateVue from './certificates.vue'


export default {
name: 'project1',
components: { 
    weather, 
    calculatorVue, 
    captionOverlayVue, 
    otherProjectVue,
    certificateVue,

},
setup(){
    onMounted(()=>{
        function reveal() {
        var reveals = document.querySelectorAll(".reveal");

        for (var i = 0; i < reveals.length; i++) {
            var windowHeight = window.innerHeight;
            var elementTop = reveals[i].getBoundingClientRect().top;
            var elementVisible = 150;

            if (elementTop < windowHeight - elementVisible) {
            reveals[i].classList.add("active");
            } else {
            reveals[i].classList.remove("active");
            }
        }
        }

        window.addEventListener("scroll", reveal);
    })
}
}
</script>

<style>
.project-section{
    height: fit-content;
    width: 100vw;
    background-color: #fcfdfe;
    display: flex;
    align-items: center;
    padding: 10px 0;
    flex-direction: column;
}

.project{
    height: 650px;
    width: 80%;
    background-color: white;
    margin: 50px 0;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.reveal {
  position: relative;
  opacity: 0;
}

.reveal.active {
  opacity: 1;
}

.active.fade-left {
  animation: fade-left 0.5s ease-in;
}
/* Weather Section */
.weather{
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.weather-caption{
  width: 30%;
  display: flex;
  justify-content: center;
  margin-right: 70px;
  position: relative;
}

.caption-overlay{
  height: 100%;
  width: 100%;
  position: absolute;
  top: -25px;
  right: 105px;
}

.caption-tile{
  height: 30%;
  width: 140%;
}

.caption-overlay .caption-tile:nth-child(1) {
  background-color: #fffcf2;
  margin-left: 10px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.caption-overlay .caption-tile:nth-child(2) {
  background-color: #fffcf2;
  margin-left: 50px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.caption-overlay .caption-tile:nth-child(3) {
  background-color: #fffcf2;
  margin-left: 30px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.caption-overlay .caption-tile:nth-child(4) {
  background-color: #fffcf2;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.weather-caption .weather-text{
  position: relative;
  letter-spacing: 1px;
}

.weather-caption .weather-text::first-letter {
    font-size: 3rem;
    color: orangered;
    float: left;
    margin-right: 0.5rem;
    line-height: 45px;
}
/* calculator */
.calculator.project{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 70px;
}

.calculator .overlay{
  transform: scaleX(-1);
  left: 100px;
}

.calculator .caption-text{
  margin-left: unset;
}

/* hove instruct */

.hove-instruct{
  background-color: #fffcf2;
  height: 60px;
  display: flex;
  align-items: center;
  margin-bottom: 100px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.hove-instruct .text{
  text-decoration: none;
  font-size: 2rem;
  margin-left: 50px;
  color: black
}

.hove-instruct .text::first-letter{
  font-size: 3rem;
  color: orangered;
}

/* Other Projects */

.otherProjects{
  margin-top: 50px;
  width: 100%;
  background-color: whitesmoke;
  
  padding: 50px 0;
}
.projectslide{
  height: fit-content;
  margin: 50px auto;
}

.project .project-card{
  height: 400px;
}

/* certificates */
.certificateDiv{
  width: 100%;
  background-color: white;
}

.certificateDiv .hove-instruct{
  margin: 0;
  margin-bottom: 20px;
  margin-top: 50px;
}

.certificate.project{
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: none;
  width: 100%;
  margin-top: 0;
  background-color: #fcfdfe00;
}

@keyframes fade-left {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}


@media only screen and (max-width: 768px) {
  .project{
    flex-direction: column;
    gap: 50px;
  }

  /* weather section */
  .weather.project, .calculator.project{
    height: fit-content;
    padding: 70px 0;
    flex-wrap: wrap;
  }

  #weather-main{
    margin-left: unset !important;
  }

  .weather-caption{
    width: 60%;
    margin-right: 0;
  }

  .caption-overlay{
    right: 85px;
  }

  /* calculator section */
  .calculator.project{
    flex-direction: column-reverse;
  }

  .calculator .caption{
    width: 60%;
  }
}
</style>