<template>
  <div id="weather-main" :class="temp">
    <div class="overlay">

      <div class="search-bar">
        <v-responsive
          class="mx-auto search-input"
          max-width="70%"
        >
          <v-text-field
            hide-details="auto"
            label="Location"
            v-model="city"
            placeholder="Search place..."
            @keypress="userInput"
            type="text"
            class = "text-field"
          ></v-text-field>
        </v-responsive>
      </div>

      <div class="location-date">
        <h2 v-if="weather.location">{{ weather.location.name }}, {{ weather.location.country }}</h2>
        <i>{{ formattedDate }}</i>
      </div>

      <div class="temperature">
        <h1 v-if="weather.location">{{weather.current.temp_c}}°c</h1>
      </div>

      <div class="temp">
        <h1><i>{{ temp }}</i></h1>
      </div> 

    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
export default {
  name: 'weather',
  setup(){
    
    const city = ref('')
    const weather = ref({});
    const apiKey = ref('b749e830424643018d303055231003')
    const temp = ref('normal')

    //date
    const date = new Date();
    const weekdays = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
    const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
    const dayOfWeek = weekdays[date.getDay()];
    const month = months[date.getMonth()];
    const dayOfMonth = date.getDate();
    const year = date.getFullYear();
    const formattedDate = `${dayOfWeek}, ${month} ${dayOfMonth} ${year}`;
    
    const getWeather = async () =>{
      try {
        const cityName = city.value ? city.value : "Philippines";
        const get = await fetch(`http://api.weatherapi.com/v1/current.json?key=${apiKey.value}&q=${cityName}&aqi=no`);
        weather.value = await get.json();
        console.log(weather.value)
        typeOfWeather();
      } catch (error) {
        console.log(error)
      }
    }

    const userInput = (e) => {
      if (e.key == "Enter"){
        if(!city.value){
          city.value = 'Philippines'
        }
        else{
          getWeather();
        }
    }
      
    }

    const typeOfWeather = () => {
      if(weather.value.current.temp_c <= 20){
        temp.value = 'cold'
      }else if(weather.value.current.temp_c >= 20 && weather.value.current.temp_c <= 30){
        temp.value = 'normal'
      }else if(weather.value.current.temp_c >= 30){
        temp.value = 'hot'
      }
    }
    
    getWeather();

    return { city, weather, formattedDate, temp, getWeather, userInput }
  }
}
</script>

<style scoped>

#weather-main{
  text-align: center;
  height: 450px;
  width: 450px;
  background-image: url("../assets/image1.png");
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  margin-left: 60px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.75);
  transition: background-image 0.5s ease-in-out;
}

#weather-main.cold{
  background-image: url("../assets/cold.jpg");
}

#weather-main.hot{
  background-image: url("../assets/hot.jpg");
}

.overlay{
  border-radius: 10px;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 50%;
  padding: 20px;
  background: rgb(0,0,0);
  background: linear-gradient(180deg, rgba(0,0,0,0.1) 0%, rgba(0,0,0,0.29744397759103647) 50%, rgba(0,0,0,0.5) 100%);
}
.search-input{
  background-color: rgba(241,250,238, 0.50);
  box-sizing: border-box;
  border-radius: 8px;
  color: #f1faee;
}
.search-input:focus-within {
  background-color: rgba(241,250,238, 0.90);
  color: #2c3e50;
  box-sizing: border-box;
  border-bottom: 2px solid black;
}


.location-date{
  color: #f1faee;
  margin-bottom: 30px;
}

.temperature{
  height: 120px;
  min-width: 140px;
  margin: 0 auto;
  background-color: rgba(241,250,238, 0.60);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.75);
  width: fit-content;
  padding: 10px;
}

.temperature h1{
  font-size: calc(8px + 3vw);
  box-sizing: border-box;
}

.temp{
  color: #f1faee;
}
</style>
