<template>
  <div class="calculator-div">
    <div class="display">{{value || 0 }}</div>
    
    <div @click="clear" class="btn"><span>C</span></div>
    <div @click="sign" class="btn"><span>+/-</span></div>
    <div @click="percent" class="btn"><span>%</span></div>
    <div class="btn side-operator"><span>÷</span></div>

    <div @click="numberAppend(`7`)" class="btn"><span>7</span></div>
    <div @click="numberAppend(`8`)" class="btn"><span>8</span></div>
    <div @click="numberAppend(`9`)" class="btn"><span>9</span></div>
    <div class="btn side-operator"><span>x</span></div>

    <div @click="numberAppend(`4`)" class="btn"><span>4</span></div>
    <div @click="numberAppend(`5`)" class="btn"><span>5</span></div>
    <div @click="numberAppend(`6`)" class="btn"><span>6</span></div>
    <div class="btn side-operator"><span>-</span></div>

    <div @click="numberAppend(`1`)" class="btn"><span>1</span></div>
    <div @click="numberAppend(`2`)" class="btn"><span>2</span></div>
    <div @click="numberAppend(`3`)" class="btn"><span>3</span></div>
    <div @click="add" class="btn side-operator"><span>+</span></div>

    <div @click="numberAppend(`0`)" class="btn zero"><span>0</span></div>
    <div @click="dot" class="btn"><span>.</span></div>
    <div class="btn side-operator"><span>=</span></div>

  </div>
</template>

<script>
import { ref } from 'vue'

export default {
  name: 'MyCalculator',
  setup(){
    //data

    const value = ref("")
    const prevValue = ref(null)
    const operator = ref(null)

    //methods
    const clear = () => value.value = "";

    const sign = () => {
      if (value.value.charAt(0) === '0'){
        value.value = value.value.slice(1)
        if(value.value.charAt(0) === '-'){
          return value.value = value.value.slice(1)
        }
        else{

          return value.value = `-${value.value}`
        }
      }else{
        if(value.value.charAt(0) === '-'){
          return value.value = value.value.slice(1)
        }
        else{
          return value.value = `-${value.value}`
        }
      }
    }

    const percent = () => {
      value.value = `${parseFloat(value.value) / 100}`
    }

    const numberAppend = (number) => {
      value.value = `${value.value}${number}`
    }

    const dot = () => {
      
      if(value.value.indexOf(".") === -1){
        numberAppend(".")
        if(value.value.indexOf(".") === 0){
          value.value = `0${value.value}`
        }
      }

    }

    const add = () => {
      operator.value = (a, b) => {
        a + b;
      }
      prevValue.value = value.value
    }

    return {value, clear, sign, percent, numberAppend, dot, add}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.calculator-div{
  height: 315px;
  border: 1px solid;
  display: grid;
  width: 300px;
  grid-template-columns: repeat(4, 1fr);
  padding: 15px;
  box-sizing: border-box;
  background-color: #14213d;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.btn{
  border: 1px solid black;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 1vh);
  background-color: #588157;
  color: white;
}

.btn:hover{
  cursor: pointer;
}

.btn:active{
  background-color: #f1faee;
  color: black;
}

.display{
  grid-column: 1 / span 4;
  border: 1px solid black;
  margin: 15px 0px;
  padding: 10px;
  font-size: calc(14px + 1vh);
  text-align: right;
  background-color: white;
}

.zero{
  grid-column: 1 / span 2;
}

.side-operator{
  background-color: #ffb703;
  color: #f1faee;
}

.side-operator:active{
  background-color: #ffb803d7;
  color: #343a40;
}
</style>
